<template>
<div class="slideshow-area" 
  :style="{width: (boxW+'px')}" 
  v-if="isError"
  @mouseenter.stop="hover_fn($event)"
  @mouseleave.stop="leave_fn($event)">
  
  <pre v-if="this.options.debug" style="font-size: 11px;">
    {{'nowIndex: ' + this.nowIndex}}
    {{'moveTime: ' + this.moveTime}}
    {{'autoTimes: ' + this.autoTimes}}
    {{'autoPlayTime: ' + this.options.autoPlayTime}}
    {{'isMove: ' + this.isMove}}
    {{this.touch}}
  </pre>

  <div class="move-area" 
    :class="{stopAnimate: isResetX}" 
    :style="{
      width: (bodyW+'px'), 
      left: (moveX+'px'),
      transition: (moveTime/1000+'s')
    }" 
    @touchstart.stop="touchstart_fn($event)"
    @touchmove.stop="touchmove_fn($event)"
    @touchend.stop="touchend_fn($event)" >

    <a :href="options.imgs[(options.imgs.length-1)].link"
      @click="options.imgs[(options.imgs.length-1)].click(options.imgs[(options.imgs.length-1)])">
      <img :src="options.imgs[(options.imgs.length-1)].path"
        :style="{width: (boxW+'px')}"/>
    </a><a v-for="(item, i) in options.imgs" :href="item.link"
      @click="item.click(item)">
      <img :src="item.path" 
        :style="{width: (boxW+'px')}"/>
    </a><a :href="options.imgs[0].link"
      @click="options.imgs[0].click(options.imgs[0])">
      <img :src="options.imgs[0].path"
        :style="{width: (boxW+'px')}"/>
    </a>

  </div>
  <!--點點-->
  <ul>
    <li v-for="(item, i) in options.imgs"
      :class="{active: i === nowIndex}"
      @click="clickDot_fn(i)" ></li>
  </ul>
  <!--左右箭頭-->
  <a class="arrow arrow-left" @click="clickArrow_fn(-1)">
    <i class="fa fa-chevron-left"></i>
  </a>
  <a class="arrow arrow-right" @click="clickArrow_fn(1)">
    <i class="fa fa-chevron-right"></i>
  </a>
</div>
</template>


<script>
/** 父層帶入 options 參數說明
   parentId     [string]          套件對應的上層 id 名稱 (必選)
   imgs         [Array]           資料集合 (必選)
                                  單一筆的資料格式
   imgs[n] {
        path: 'xxxx',
        link: 'xxxx',
        click: function(){} 
   }

   moveTime     [Number]          動畫移動的毫秒數 (參選) (限制不可小於200)
   startIndex   [Number/String]   用第幾張為起始點 (參選) (索引數0為第一張圖)
   autoPlayTime [Number]          自動播放動畫秒數 (參選) (限制不可小於 moveTime)
   debug        [Boolean]         開發套件觀看套件狀態用
*/
export default {
  props: ['options'],
  data () {
    return {
      nowIndex: 0,          //紀錄現在播放到第幾張圖片(預設第一張圖)
      moveX: 0,             //當下要移動的座標
      isResetX: false,      //是否在切換頭尾造假的狀態
      isMove: false,        //是否正在切換的狀態
      moveTime: 1000,       //動畫豪秒數 (限制不可小於200)
      autoTimes: null,       //預計放自動播放的setInterval物件用
      spaceTime: 60,        //因應頭尾造假作的秒數延遲參數  //TODO...時間還要再抓過

      parentDOM: null,      //主要放套件的父層容器 (透過 parentId 找到的DOM)
      paddingL: 0,
      paddingR: 0,
      boxW: 0,              //可視範圍的大小
      bodyW: 0,             //左右移動的容器大小
      isError: false,       //組件初始化檢查過程是否有錯誤

      //針對觸控作的物件
      touch: {
        originalX: 0,              //紀錄 touchstart 時原本的 moveX 數值
        originalMoveTime: null,    //記錄 touchstart 時原本的 動畫秒數 數值
        notXmove: false,           //紀錄 當上下移動太大時不給左右移動的依據
        startX: 0,                 //紀錄 touchstart 的 x 座標
        startY: 0,                 //紀錄 touchstart 的 y 座標
        endX: 0,                   //紀錄 touchmove 的 x 座標
        endY: 0,                   //紀錄 touchmove 的 y 座標
        gapX: 0,                   //左右移動的間距
        gapY: 0,                   //上下移動的間距
        spaceX: 50,                //判段是左右的限制大小  //TODO...大小還要再抓過
        spaceY: 20                 //判段是上下的限制大小  //TODO...大小還要再抓過
      },
      copyTouch: null                //紀錄touch物件一開始的初始化設定用
    }
  },
  methods: {
    //click 點點
    clickDot_fn (index) {
      if (!this.isMove && this.nowIndex !== index) {
        this.nowIndex = index;
        this.calcX_fn();
      }
    },
    //click 左右箭頭
    clickArrow_fn (i) {
      if (!this.isMove) {
        this.nowIndex = this.nowIndex + i;
        this.calcX_fn();
      }
    },
    //滑鼠移入
    hover_fn (event) {
      this.clearAutoTimes_fn();
    },
    //滑鼠移出
    leave_fn (event) {
      this.autoPlayInit();
    },
    //自動輪播方法
    autoPlay_fn () {
      if (!this.isMouseHover && !this.isMove && !this.touch.startX) {
        this.clickArrow_fn(1);
      }
    },
    //初始化自動輪播方法
    autoPlayInit () {
      this.clearAutoTimes_fn(); //強制清掉之前的註冊
      if (this.options.autoPlayTime != null) {
        //自動播放時間不可小於等於動畫秒數
        if (this.options.autoPlayTime <= this.moveTime) {
          this.options.autoPlayTime = this.moveTime + 200; //200是彈性值
        }
        this.autoTimes = setInterval(this.autoPlay_fn, this.options.autoPlayTime);
      }
    },
    //清除自動播放
    clearAutoTimes_fn () {
      clearInterval(this.autoTimes); 
      this.autoTimes = null;
    },
    //計算左右移動
    calcX_fn () {
      this.clearAutoTimes_fn();
      this.isMove = true;
      //頭部造假
      if (this.nowIndex < 0) {
        this.moveX = 0 * this.boxW;

        setTimeout(() => {
          this.isResetX = true; //停止css的動化秒數
          this.nowIndex = this.options.imgs.length - 1; //回到最後一筆
          this.moveX = -(this.nowIndex + 1) * this.boxW;
        }, this.moveTime);
        setTimeout(() => {
          this.isResetX = false; //打開css的動化秒數
          this.isMove = false;
          this.autoPlayInit();
        }, (this.moveTime + this.spaceTime));
      }
      //正常範圍
      if (this.nowIndex >= 0 && this.nowIndex < this.options.imgs.length) {
        this.isResetX = false; //打開css的動化秒數
        this.moveX = -(this.nowIndex + 1) * this.boxW;
        setTimeout(() => {
          this.isMove = false;
          this.autoPlayInit();
        }, (this.moveTime + this.spaceTime));
      }
      //尾部造假
      if (this.nowIndex >= this.options.imgs.length) {
        this.moveX = -(this.options.imgs.length + 1) * this.boxW;

        setTimeout(() => {
          this.isResetX = true; //停止css的動化秒數
          this.nowIndex = 0; //回到第一筆
          this.moveX = -(this.nowIndex + 1) * this.boxW;
        }, this.moveTime);
        setTimeout(() => {
          this.isResetX = false; //打開css的動化秒數
          this.isMove = false;
          this.autoPlayInit();
        }, (this.moveTime + this.spaceTime));
      }
    },
    //瀏覽器尺寸監聽方法
    listener () {
      //停止自動輪播
      this.clearAutoTimes_fn();
      //套件寬度初始化
      this.boxW = this.parentDOM.clientWidth - (this.paddingL + this.paddingR);
      //+2是為了頭尾造假用的圖片  *1.5是因為瀏覽器放大時會有高度排擠的現象
      this.bodyW = ((this.options.imgs.length + 2) * (this.boxW * 1.5));
      this.defaultX_fn();
    },
    //觸控事件: 按下
    touchstart_fn (event) {
      var touch = this.touch;
      this.clearAutoTimes_fn();
      if (!this.isMove && !touch.startX) {
        touch.originalX = this.moveX;
        touch.originalMoveTime = this.moveTime;
        this.moveTime = 0;
        touch.startX = Math.ceil(event.touches[0].screenX);
        touch.startY = Math.ceil(event.touches[0].screenY);
      }
    },
    //觸控事件: 按住移動
    touchmove_fn (event) {
      var touch = this.touch;
      this.clearAutoTimes_fn();
      if (!this.isMove && touch.startX) {
        touch.endX = Math.ceil(event.touches[0].screenX);
        touch.endY = Math.ceil(event.touches[0].screenY);
        touch.gapX = Math.ceil(touch.startX - touch.endX);
        touch.gapY = Math.ceil(touch.startY - touch.endY);

        //判斷上下移動距離是否超過一定大小, 太大就不做左右移動效果
        if (Math.abs(touch.gapY) > touch.spaceY) {
          touch.notXmove = true;
        }
        if (!touch.notXmove) {
          this.moveX = this.touch.originalX - touch.gapX; //做出隨手指左右移動的效果
        } else {
          this.moveX = this.touch.originalX; //上下移動太多, 不給左右移動的效果
          this.moveTime = touch.originalMoveTime;
        }
      }
    },
    //觸控事件: 放開
    touchend_fn (event) {
      var touch = this.touch;
      this.clearAutoTimes_fn();            
      if (!this.isMove && touch.startX) {
        touch.gapX = Math.ceil(touch.startX - touch.endX);
        touch.gapY = Math.ceil(touch.startY - touch.endY);

        //判斷左右移動是否過小
        if (Math.abs(touch.gapX) < touch.spaceX) {
          this.moveTime = 300;
          this.moveX = touch.originalX; //恢復原座標
          setTimeout(() => {
            this.moveTime = touch.originalMoveTime;
            this.autoPlayInit();
          }, 300);
        } else {
          this.moveTime = touch.originalMoveTime;
          //沒有上下移動太多 並且 有移動過
          if (!touch.notXmove) {
            if (touch.endX) {
              //要往左或往右
              if (touch.startX - touch.endX < 0) {
                this.clickArrow_fn(-1);
              } else {
                this.clickArrow_fn(1);
              }
            }
          }
          this.autoPlayInit();
        }
          
        //touch物件恢復預設值
        this.touch = JSON.parse(JSON.stringify(this.copyTouch));
      } else {
        this.autoPlayInit();
      }
    },
    //基本座標
    defaultX_fn () {
      this.moveTime = 0;
      this.moveX = -(this.nowIndex + 1) * this.boxW; // +1 是因為頭尾造假
      setTimeout(() => {
        this.checkMoveTime();
        if (!this.autoTimes) {
          this.autoPlayInit();
        }
      }, 0);
    },
    //檢查動化秒數
    checkMoveTime () {
      this.moveTime = (!this.options.moveTime) ? 1000 : this.options.moveTime;
      if (this.moveTime < 200) {
        this.moveTime = 200;
      }
    },
    //套件初始化方法
    init () {
      //檢查是否有帶父層容器
      this.parentDOM = document.getElementById(this.options.parentId);
      if (this.options.parentId) {
        this.isError = true;
      }

      //檢查動畫毫秒數
      this.checkMoveTime();

      //用第張圖當作起始圖 (預設第一張圖)
      this.nowIndex = (this.options.startIndex !== null) ? this.options.startIndex : 0;

      //相關影響寬度的數值
      this.paddingL = parseInt(window.getComputedStyle(this.parentDOM, null).getPropertyValue('padding-left'), 10);
      this.paddingR = parseInt(window.getComputedStyle(this.parentDOM, null).getPropertyValue('padding-right'), 10);

      //執行一次寬度計算
      this.listener();

      //紀錄一開始的touch物件設定值
      this.copyTouch = JSON.parse(JSON.stringify(this.touch));

      //基本座標
      this.defaultX_fn();

      //自動播放
      //this.autoPlayInit();
    }
  },
  mounted () {
    this.init();
    window.addEventListener('resize', this.listener, false);
    window.addEventListener("orientationchange", this.listener ,false);
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.listener, false);
    window.removeEventListener("orientationchange", this.listener ,false);
  },
  watch: {}
}
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>


