<template>
<div class="dialog-wrapper" v-show="show" :class="{'zoom': zoomShow}">
  <div class="dialog-mask" @click="closeFn" v-show="maskShow"></div>
  <div class="dialog" :class="'dialog-size-'+size">
    <div class="dialog-btn">
      <i class="fa fa-window-restore" @click="zoomFn" v-show="zoom" v-if="zoomShow"></i>
      <i class="fa fa-window-restore" @click="zoomFn" v-show="zoom" v-if="!zoomShow"></i>
      <i class="fa fa-times" @click="closeFn"></i>
    </div>
    <div class="dialog-scroll">
      <slot></slot>
    </div>
  </div>
</div>
</template>

<script>
/** 父層帶入 options 參數說明
    show       [boolean]    是否打開 dialog  (預設 false )
    zoom       [boolean]    是否使用縮放功能  (預設 false )
    maskShow   [boolean]    是否打開 dialog 背景遮罩  (預設 true )
    zoomShow   [boolean]    是否在縮放狀態  (預設 false )
    size       [string]     dialog 尺寸大小 ('S', 'M', 'L') (預設 'S' )
    closeCall  [function]   關閉dialog後執行的callback
*/
export default {
  props: ['options'],
  data () {
    return {
      show: false,
      zoom: false,
      maskShow: true,
      zoomShow: false,
      size: 's',
      closeCall() {}
    }
  },
  methods: {
    closeFn () {
      this.show = false;
      this.options.show = false;
      this.closeCall();
    },
    zoomFn () {
      this.zoomShow = (!this.zoomShow);
    },
    init () {
      let props = this.options;
      //提醒這邊預設值要注意 null 跟 undefined 的陷阱
      this.show = (props.show != null)? props.show : this.show;
      this.zoom = (props.zoom != null)? props.zoom : this.zoom;
      this.maskShow = (props.maskShow != null)? props.maskShow : this.maskShow;
      this.zoomShow = (props.zoomShow != null)? props.zoomShow : this.zoomShow;
      this.size = (props.size != null)? props.size : this.size;
      this.closeCall = (props.closeCall != null)? props.closeCall : this.closeCall;
    }
  },
  mounted () {
    this.init();
  },
  watch: {
    'options': {
      deep: true,
      handler () {
        this.init();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>


