<template>
  <div id="app" class="wrapper">
    <!-- ajax -->
    <span class="ajaxLogin" v-show="$root.ajaxCount > 0">
        <i class="fa fa-spinner fa-spin"></i>
    </span>

    <header>Lucas - Vue component overview</header>
    <div class="bread">首頁 / GUI</div>
    <div class="container">
      <router-view/>
    </div>
    <footer>© {{year}} Lucas. All rights reserved</footer>
  </div>
</template>

<script>
/*這邊axios參考了:
https://kuro.tw/posts/2017/06/07/%E5%A6%82%E4%BD%95%E5%9C%A8-Vue-CLI-%E5%BB%BA%E7%AB%8B%E7%9A%84%E9%96%8B%E7%99%BC%E7%92%B0%E5%A2%83%E5%91%BC%E5%8F%AB%E8%B7%A8%E5%9F%9F%E9%81%A0%E7%AB%AF-RESTful-APIs/
*/
import Vue from 'vue'
import axios from 'axios'
import axioajaxDefines from './ajaxDefine'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios)

Vue.mixin(axioajaxDefines) // 將使用ajax的通用設定提供給 vue

export default {
  name: 'App',
  data () {
    return {
      year: new Date().getFullYear()
    }
  },
  created () {
    /* 原本測試跨 CROS 路徑: http://data.taipei/youbike
       經過 /config/index.js 理面的 proxyTable['/data'] 設定過後就變成: /data/youbike
    */
    this.rootHttpGet('/data/youbike').then((response) => {
      console.log(response);
    })
  },
}
</script>

<style lang="scss" scoped>
  /* 不想將 style.main.scss 放到 main.js 是因為我發現現在 webpack 等等的開發環境常常在變, 
     網路上找到的資料都是還要自己手動回頭去改 config 檔, 這樣在別人拿到專案時無法直接 npm install.
  */
  @import './scss/style.main.scss';

</style>



