import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/layouts/HelloWorld'
import Gui from '@/layouts/Gui'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'gui',
      component: Gui
    },
    {
      path: '/Hello',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



