
<template>
<div class="sliderbar-area">
  <div class="bar" :style="{width: (nowW+'px')}" ></div>
  <div class="ball"
    :class="{isActive: isDaggle}"
    @mousedown="mouseDown_fn($event)"
    @touchstart="touchstart_fn($event)"
    :style="{left: (nowX+'px')}"
    :datanum="nowNum"></div>
</div>
</template>

<script>
/** 父層帶入 options 參數說明
    result   [Number]    外部指定的初始數值
    minNum   [Number]    限制最小數值
    maxNum:  [Number]    限制最大數值
    callback [function]  回呼函式 (套件回傳 nowNum)

    註解: 目前此套件只接受 0 - 100 % 的概念, 不接受小數點的概念.
*/
export default {
  props: ['options'],
  data () {
    return {
      nowNum: 0,           // 當下座標轉成數值
      nowW: 0,             // 當下數值轉成寬度
      nowX: 0,             // 當下的座標
      startX: null,        // 滑鼠按下的x座標
      endX: null,          // 滑鼠放開的x座標
      boxW: 0,             // 組件整體的寬度
      ballW: 0,            // 組件ball的寬度
      isDaggle: false,
      resizeTimes: null    // 當瀏覽器 resize 使用的計數器物件
    }
  },
  methods: {
    // 滑鼠按下
    mouseDown_fn (event) {
      this.isDaggle = true;
      this.startX = event.screenX;
    },
    // 滑鼠放開
    mouseUp_fn (event) {
      this.isDaggle = false;

      //由於有註冊事件到 document 所以整個網頁都會觸發套件的事件, 這邊判斷沒有值就不執行 callback
      if (!this.startX || !this.endX) {
        return false;
      }

      this.options.callback(this.nowNum);
      this.startX = null;
      this.endX = null;
    },
    // 滑鼠移動
    mouseMove_fn (event) {
      if (!this.isDaggle) {
        return false;
      }
      if (this.endX === event.screenX) {
        return false;
      }
      setTimeout(() => {
        this.endX = event.screenX;
        this.calc_fn();
      }, 60);
    },
    // 觸控事件: 按下
    touchstart_fn (event) {
      this.isDaggle = true;
      this.startX = event.touches[0].screenX;
    },
    // 觸控事件: 按住移動
    touchmove_fn (event) {
      if (!this.isDaggle) {
        return false;
      }
      if (this.endX === event.touches[0].screenX) {
        return false;
      }
      this.endX = event.touches[0].screenX;
      this.calc_fn();
    },
    // 觸控事件: 放開
    touchend_fn (event) {
      this.isDaggle = false;

      //由於有註冊事件到 document 所以整個網頁都會觸發套件的事件, 這邊判斷沒有值就不執行 callback
      if (!this.startX || !this.endX) {
        return false;
      }

      this.options.callback(this.nowNum);
      this.startX = null;
      this.endX = null;
    },
    // 計算拖曳中的值得換算方法
    calc_fn () {
      //● 處理座標
      this.nowX = this.nowX - (this.startX - this.endX);
      if (this.nowX < 0) {
        this.nowX = 0;
      }
      if (this.nowX > (this.boxW - this.ballW)) {
        this.nowX = (this.boxW - this.ballW);
      }
      this.startX = this.endX;

      //● 處理座標轉數值
      this.nowNum = Math.ceil((this.nowX / (this.boxW - this.ballW)) * 100);
      //限制最小最大數值範圍
      if (this.options.minNum != null) {
        if (this.nowNum < this.options.minNum) {
          this.nowNum = this.options.minNum;
          this.numToX_fn();//在將座標轉回去
        }
      }
      if (this.options.maxNum != null) {
        if (this.nowNum > this.options.maxNum) {
          this.nowNum = this.options.maxNum;
          this.numToX_fn();//在將座標轉回去
        }
      }

      //● 處理現在的數值轉程多少寬度
      this.numToW_fn();
      if (this.nowW < 0) {
        this.nowW = 0;
      }
      if (this.nowW >= (this.boxW - this.ballW / 2)) {
        this.nowW = (this.boxW - this.ballW / 2);
      }
    },
    // 數值轉座標方法
    numToX_fn () {
      this.nowX = this.nowNum * ((this.boxW - this.ballW) / 100);
    },
    // 數值轉寬度
    numToW_fn () {
      this.nowW = Math.ceil(this.nowX);
    },
    // 當瀏覽器變換大小
    listener () {
      this.boxW = this.$el.clientWidth;   // 組件的寬度
      // 避免觸發太多次
      if (!this.resizeTimes) {
        this.resizeTimes = setTimeout(() => {
          // 處理數值轉座標
          this.numToX_fn();
          // 處理現在的數值轉程多少寬度 // 不能用 calc_fn 因為沒有 startX 跟 endX
          this.numToW_fn();
          clearTimeout(this.resizeTimes);
          this.resizeTimes = null;
        }, 100);
      }
    },
    // 套件初始化
    init () {
      this.ballW = this.$el.children[1].clientWidth;  // 球的寬度
      // 外部給值, 套件預設 0
      this.nowNum = (this.options.result) ? (this.options.result) : 0;
      // 外部沒有給回呼函式自己預設一個空方法
      if (!this.options.callback) {
        this.options.callback = function () { };
      }
      this.numToX_fn();
      this.listener();
    }
  },
  mounted () {
    this.init();
    window.addEventListener('resize', this.listener, false);
    document.addEventListener('mousemove', this.mouseMove_fn, false);
    document.addEventListener('mouseup', this.mouseUp_fn, false);
    document.addEventListener('touchmove', this.touchmove_fn, false);
    document.addEventListener('touchend', this.touchend_fn, false);
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.listener, false);
    document.removeEventListener('mousemove', this.mouseMove_fn, false);
    document.removeEventListener('mouseup', this.mouseUp_fn, false);
    document.removeEventListener('touchmove', this.touchmove_fn, false);
    document.removeEventListener('touchend', this.touchend_fn, false);
  },
  watch: {
    //監測外部的參數是否有異動
    'options': {
      deep: true, //深度監測的意思
      handler () {
        this.init();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import './style.scss';
</style>


