<template>
<div class="datapicker_area" >
  <input type="text" 
    :value="result"
    :placeholder="placeholder"
    :disabled="disabled"
    @focus="show_fn($event)"
  >
  <div :class="{'datapicker_box': true, 'right': isRight}" v-show="isShow">

    <div class="datapicker_top_box">
      <!-- 上個月 -->
      <div class="prev_btn" @click="prevMonth_fn()">
        <i class="fa fa-chevron-left" aria-hidden="true"></i>
      </div>
      <span>
        <i v-text="searchYear"></i> - 
        <i v-text="(searchMonth+1)"></i> 
      </span>
      <!-- 下個月 -->
      <div class="next_btn" @click="nextMonth_fn()">
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </div>
    </div>

    <ul class="thead">
      <li v-for="item in theadAry"><span>{{item.weekTxt}}</span></li>
    </ul>

    <!-- 套件原本的結構 -->
    <ul class="tbody" v-if="!useSlot">
      <li v-for="item in datesAry"
        :class="{
          active: (item.yearTxt === choiseYear &&
                  item.dayTxt === choiseDay &&
                  item.monthTxt === choiseMonth),
          disable: checkDtaeIsDisable_fn(item)
        }"
        @click="cliickDate_fn(item)">
        <span>{{item.dayTxt}}</span>
      </li>
    </ul>
      
    <!-- 外部設定指定 html 結構, 只開放外部用 li 的部分 ul 還是定義在套件中 -->
    <ul class="tbody" v-if="useSlot">
      <slot name="dateSlot" 
        :dateReturn="datesAry" 
        :clickDate="cliickDate_fn" 
        :checkDate="checkDtaeIsDisable_fn">
      </slot>
    </ul>

  </div>
  <div class="datapicker_maskbg" v-show="isShow" v-if="!keepshowPicker" 
    @click="hide_fn()"></div>

</div>
</template>

<script>
// [v] 語系包
const datepickerLg = {
  "tw": [
    { weekTxt: '日' },
    { weekTxt: '一' },
    { weekTxt: '二' },
    { weekTxt: '三' },
    { weekTxt: '四' },
    { weekTxt: '五' },
    { weekTxt: '六' }
  ],
  "ch": [
    { weekTxt: '日' },
    { weekTxt: '一' },
    { weekTxt: '二' },
    { weekTxt: '三' },
    { weekTxt: '四' },
    { weekTxt: '五' },
    { weekTxt: '六' }
  ],
  "en": [
    { weekTxt: 'Su' },
    { weekTxt: 'Mo' },
    { weekTxt: 'Tu' },
    { weekTxt: 'We' },
    { weekTxt: 'Th' },
    { weekTxt: 'Fr' },
    { weekTxt: 'Sa' }
  ]
};

/** 父層帶入 參數說明
  result          [必選]  套件回傳被選取的日期 
  disabled        [參選]  input 欄位是否不可編輯
  placeholder     [參選]  placeholder
  mindate         [參選]  可選最小日期
  maxdate         [參選]  可選最大日期
  callback        [參選]  選取日期之後的 callback
  lang            [參選]  多語系的代號只接受 tw, ch, en 三種代號, 語言包在套件內設定不由外部提供
  keepshowPicker  [參選]  是否常態性呈現小日歷
  useSlot         [參選]  外部是否使用 slot 區塊去自定義 li 的 render
  
  目前套件限制: 
      日期的格式化固定使用: 2017-04-06
      常態性呈現小日歷的情況下input的輸入欄位跟mask是隱藏起來的
      常態性呈現小日歷的情況下,小日歷就不是浮動的
  目前套件並沒有提供  大幅度的年份與月分切換 (偷懶)
  目前套件依賴於 language 所以無法直接套用在別的專案上 (之後在想要怎麼改)
*/
export default {
  props: {
    result: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    mindate: {
      type: String,
      default: ''
    },
    maxdate: {
      type: String,
      default: ''
    },
    callback: {
      type: Function,
      default: null
    },
    lang: {
      type: String,
      default: 'tw'
    }, 
    keepshowPicker: {
      type: Boolean,
      default: false
    },
    useSlot: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // 小日曆是否呈現
      isShow: false,
      // 小日曆打開後是否超過螢幕的右邊了
      isRight: false,
      // 記錄套件搜尋的年月日
      searchYear: 2018,
      searchMonth: 5,
      searchDay: 22,
      // 記錄使用者點選之後的年月日
      choiseYear: null,
      choiseMonth: null,
      choiseDay: null,
      // 記錄小日曆中每一天的資料用
      datesAry: [],
      // 小日曆中星期幾抬頭用 (預設用語系包的tw)
      theadAry: datepickerLg.tw,
    }
  },
  methods: {
    // [F] 查詢某年某月的小日曆所有 day 方法(主要小日曆核心方法)
    getAll_Reservation_fn (get_y, get_m) {
      this.datesAry = []; // 從新來過

      let searchDate,     // 記錄日期物件用
          firstWeek,      // 記錄本月從星期幾開始用
          allDay,         // 記錄本月總天數用
          vacancyPrev,    // 要往前補的天數
          vacancyNext,    // 要往後補的天數
          i = null;

      searchDate = new Date(get_y, get_m, 1);
      firstWeek = searchDate.getDay();

      // 先把月份跳成下一個月，這樣setDate(0)就可取得上個月的最後一天，也就是本月有幾天
      searchDate.setMonth(searchDate.getMonth() + 1);
      searchDate.setDate(0);
      allDay = searchDate.getDate();

      // 處理要補的天數
      vacancyPrev = (7 - (7 - firstWeek));  // 一排7天 - 依照星期幾開始往前加上幾個空白天數
      vacancyNext = (7 - ((allDay + vacancyPrev) % 7)); // 一排7天 - 依照所有天數 + vacancyPrev 除以7取餘數

      // 開始準備給 ng-repeat 用的資料
      for (i = 0; i < allDay + vacancyPrev + vacancyNext; i++) {
        let oneDayObj = {
            dayTxt: '',      // 小日曆中td呈現的日期
            monthTxt: '',    // 小日曆中td呈現的日期所屬的月份
            yearTxt: '',     // 小日曆中td呈現的日期所屬的年份
            fullValue: null  // 小日曆中td的日期完整的日期字串(月日有補0)
        },
        tempDayTxt,          // 取值用過度字串
        tempMonthTxt;        // 取值用過度字串

        // 是否本月的天數中(避掉要補的天數)
        if ((i > vacancyPrev - 1) && (i < vacancyPrev + allDay)) {
          tempDayTxt = (i - vacancyPrev) + 1;
          tempMonthTxt = this.searchMonth + 1; // 提醒: 月份取得的會是-1的值, 比如8月取得的會是7所以+1是為了呈現用

          // 日補0
          if (tempDayTxt <= 9) {
            tempDayTxt = '0' + tempDayTxt;
          }

          // 月補0
          if (tempMonthTxt <= 9) {
            tempMonthTxt = '0' + tempMonthTxt;
          }

          oneDayObj.dayTxt = (i - vacancyPrev) + 1;  // 不能用tempDayTxt是因為小日曆不使用補0的
          oneDayObj.monthTxt = this.searchMonth;  // 不能用tempMonthTxt是因為小日曆不使用補0的
          oneDayObj.yearTxt = this.searchYear;
          oneDayObj.fullValue = this.searchYear + '-' + tempMonthTxt + '-' + tempDayTxt;  // 固定走2017-04-16這樣的格式
        }

        this.datesAry.push(oneDayObj);
      }
      // 測試用
      //console.log(this.datesAry);
      //console.log(searchDate);
      //console.log("從星期: " + firstWeek);
      //var startDAY = get_y + "-" + (parseInt(get_m)+1) + "-1";  // 起始日
      //var endDAY = get_y + "-" + (parseInt(get_m)+1) + "-" + (searchDate.getDate());  // 結束日
      //console.log('要往前補的天數: ' + vacancyPrev);
      //console.log('要往後補的天數: ' + vacancyNext);
      //console.log('該月起始日: ' + startDAY);
      //console.log('該月結束日: ' + endDAY);
      //console.log('該月總共天數: ' + allDay);
    },
    // [E] 上一個月
    prevMonth_fn () {
      if (this.searchMonth === 0) {
        //前一年
        this.searchYear = this.searchYear - 1;
        this.searchMonth = 12;
      }
      this.searchMonth = this.searchMonth - 1;
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);
    },
    // [E] 下一個月
    nextMonth_fn () {
      if (this.searchMonth === 11) {
        // 下一年
        this.searchYear = this.searchYear + 1;
        this.searchMonth = -1;
      }
      this.searchMonth = this.searchMonth + 1;
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);
    },
    // [E] 出現小日曆
    show_fn (event) {
      this.isShow = true;
      // 由於小日曆 disable:none 取不到 clientWidth 所以先打開之後延遲了一下取值
      setTimeout(() => {
        // 計算小日曆打開後是否超過螢幕的右邊了
        let parents = event.target.parentNode.childNodes,
            $datepickerBox = parents[2],
            maxWidth = window.innerWidth,
            left = $datepickerBox.getBoundingClientRect().x,
            width = $datepickerBox.getBoundingClientRect().width;
        
        this.isRight = ((left + width) >= maxWidth)? true : false;
        this.inputKeyInCheck_fn(); // 利用此方法檢查外部是否有改變值
      }, 0);
    },
    // [E] 隱藏小日曆
    hide_fn () {
      if (!this.keepshowPicker) {
        this.isShow = false;
        this.isRight = false;
        this.inputKeyInCheck_fn(); // 檢查使用者自己key in後的值
      }
    },
    // [F] 將日期字串轉成年月日的物件
    stringToDate_fn (_txt) {
      let re = {
              year: null,
              month: null,
              day: null
          },
          d = new Date(_txt);
      if (_txt !== null) {
        re = {
          year: d.getFullYear(),
          month: d.getMonth(),  // 提醒取到的8月會是7
          day: d.getDate()
        };
      }
      return re;
    },
    // [F] 檢查使用者 key in 後的方法
    inputKeyInCheck_fn () {
      let getVal = this.stringToDate_fn(this.result), // 將使用者輸入的字串轉為年月日物件
          resetChoise_fn = (self) => {
            //清空選擇的年月日
            self.choiseYear = null;
            self.choiseMonth = null;
            self.choiseDay = null;
          },
          validate = true;

      // 如果使用者清空了
      if (this.result === '' || this.result === null) {
        validate = false;
      }

      // 如果輸入字串長度不對
      if (validate) {
        if (this.result.length < 10 || this.result.length > 10) {
          validate = false;
        }
      }

      // 如果使用者輸入的字串不是日期格式
      if (isNaN(getVal.year) || isNaN(getVal.month) || isNaN(getVal.day)) {
        validate = false;
      }

      // 檢察是否在限制範圍日期中
      if (validate) {
        let check = this.checkDtaeIsDisable_fn({ fullValue: this.result });
        if (check) {
          validate = false;
        }
      }
      
      // 最後做 model 的控制
      if (validate) {
        this.choiseYear = getVal.year;
        this.choiseMonth = getVal.month;
        this.choiseDay = getVal.day;
      } else {
        resetChoise_fn(this);
        // 清空返回的值
        this.$emit('update:result', null); // 限制上層用 .sync 的方式去觸發同步
      }

      // 外部是否有帶callback參數並且是function
      if (typeof this.callback === 'function') {
        this.callback();
      }
    },
    // [E] 當click了一個日期
    cliickDate_fn (item) {
      if (item.fullValue) {
        // 如果是同一天就不走後面程序
        if (this.result === item.fullValue) {
          return false;
        }

        // 如果不在可選範圍就不走後面程序
        if (this.checkDtaeIsDisable_fn(item)) {
          return false;
        }

        // 給予選擇的年月日
        this.choiseYear = item.yearTxt;
        this.choiseMonth = item.monthTxt;
        this.choiseDay = item.dayTxt;

        // 給予相向綁定的值
        this.$emit('update:result', item.fullValue); // 限制上層用 .sync 的方式去觸發同步

        // 外部是否有帶 callback 參數並且是 function
        if (typeof this.callback === 'function') {
          this.callback();
        }
      }
    },
    // [F] 將日期字串轉成秒數的方法
    stringToDateTime_fn (_txt) {
      let re = new Date(_txt);
      return re.getTime();
    },
    // [F] 檢察單一個日期是否在限制範圍
    checkDtaeIsDisable_fn (item) {
      let isDisable = false,  // 記錄比對結果
          checkDate,  // 字串轉日期物件用 min, max共用
          itemDate = this.stringToDateTime_fn(item.fullValue);   // 字串轉日期物件用 item 用

      // 檢察是否有限制 可選最小日期
      if (this.mindate) {
        // 將字串轉為秒數後比對
        checkDate = this.stringToDateTime_fn(this.mindate);
        if (itemDate < checkDate) {
          isDisable = true;
        }
      }
      // 檢察是否有限制 可選最大日期
      if (this.maxdate) {
        // 將字串轉為秒數後比對
        checkDate = this.stringToDateTime_fn(this.maxdate);
        if (itemDate > checkDate) {
          isDisable = true;
        }
      }
      return isDisable;
    }
  },
  mounted () {
    let today, creatData;

    // 檢查語系代號
    if (this.lang) {
      if (this.lang !== 'tw' && this.lang !== 'ch' && this.lang !== 'en') {
        console.log('語系代號錯誤,強制使用tw');
      }
    }
    this.theadAry = datepickerLg[this.lang];

    // 檢查 placeholder
    if (this.placeholder) {
      if (typeof this.placeholder !== 'string') {
        console.log('placeholder資料型別錯誤必須為字串, 強制使用空字串');
      }
    }

    // 檢查是否常態性呈現小日歷
    this.isShow = (this.keepshowPicker)? true : false;

    // 檢查 result
    if (!this.result) {
      this.result = ''; // 預設空字串
      // 沒有設定, 小日曆使用當日
      today = new Date();
      // 給予查詢的年月日
      this.searchYear = today.getFullYear();
      this.searchMonth = today.getMonth();
      this.searchDay = today.getDate();
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);
    } else {
      //有設定 要將變數拆解成 年, 月, 日的字段, 在從新取值(去掉0開頭字串)
      /* var ary = this.result.split('-'),
        creatData = new Date(ary[0], ary[1], ary[2]); */
      creatData = new Date(this.result);
      // 給予查詢的年月日
      this.searchYear = creatData.getFullYear();
      //this.searchMonth = creatData.getMonth() - 1;  //提醒: 月份取得的會是-1的值, 比如8月取得的會是7
      this.searchMonth = creatData.getMonth();
      this.searchDay = creatData.getDate();
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);

      // 給予選擇的年月日
      this.choiseYear = this.searchYear;
      this.choiseMonth = this.searchMonth;
      this.choiseDay = this.searchDay;
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../../scss/style.main.scss'; //TODO... 這怎麼辦還要自己手動將框架的全域scss檔載入才會有作用哩...
  @import './style.scss';
</style>


