<template>
<div class="pagination-area">
  <div v-if="options.debug" class="pagination-debug">
    <div>資料總筆數: {{totalcount}}</div>
    <div>單一頁要顯示幾筆資料: {{pcount}}</div>
    <div>最大頁碼數: {{maxPageNum}}</div>
    <div>套件一次顯示的頁碼數量: {{pagenum}}</div>
    <div>現在在第幾頁: {{nowpage}}</div>
  </div>
  <div class="page-first" v-if="options.firsthide" @click="firstClickFn()">
    <!-- <i class="" >最前頁</i> -->
    <i class="fa fa-angle-double-left" aria-hidden="true"></i>
  </div>
  <div class="page-prev" v-if="options.prevhide" @click="prevClickFn()">
    <!-- <i class="" >前一頁</i> -->
    <i class="fa fa-angle-left" aria-hidden="true"></i>
  </div>
  <div class="page-box">
    <a href="javascript:;" v-for="item in pageNumAry" @click="theClick((item))" 
      :class="{nowstyle: nowpage == item}">{{item}}</a>
  </div>
  <div class="page-next" v-if="options.nexthide" @click="nextClickFn()">
    <!-- <i class="" >下一頁</i> -->
    <i class="fa fa-angle-right" aria-hidden="true"></i>
  </div>
  <div class="page-last" v-if="options.lasthide" @click="lastClickFn()">
    <!-- <i class="" >最末頁</i> -->
    <i class="fa fa-angle-double-right" aria-hidden="true"></i>
  </div>
</div>
</template>

<script>
/** 父層帶入 options 參數說明
    firsthide    [boolean]         最前頁按鈕是否隱藏, 預設true  (參選)  
    lasthide     [boolean]         最末頁頁按鈕是否隱藏, 預設true  (參選)
    prevhide     [boolean]         上一頁頁按鈕是否隱藏, 預設true  (參選)
    nexthide     [boolean]         下一頁頁按鈕是否隱藏, 預設true  (參選)
    totalcount   [string/number]   資料總筆數  (必選)
    pcount       [string/number]   一頁要顯示幾筆資料  (必選)
    nowpage      [string/number]   現在在第幾頁  (必選)
    pagenum      [string/number]   套件一次顯示幾顆頁碼, 預設10 顆  (參選)
    pageclickfn  [function]        頁碼的click方法  (必選)
    debug        [boolean]         顯示 debug 用資訊介面
*/
export default {
  props: ['options'],
  data () {
    return {
      totalcount: 0,
      pcount: 0,
      maxPageNum: 0,
      pagenum: 0,
      nowpage: 0,
      pageNumAry: []
    }
  },
  methods: {
    // [E] click 頁碼方法
    theClick (pagenum) {
      this.options.pageclickfn(pagenum);
    },
    // [E] click 最前頁方法
    firstClickFn () {
      this.options.pageclickfn(1);
    },
    // [E] click 最末頁方法
    lastClickFn () {
      this.options.pageclickfn(this.maxPageNum);
    },
    // [E] click 上一頁方法
    prevClickFn () {
      let gotoNum = (parseInt(this.nowpage, 10) - 1 <= 0)? 1 : (parseInt(this.nowpage, 10) - 1);
      this.options.pageclickfn(gotoNum);
    },
    // [E] click 下一頁方法
    nextClickFn () {
      let gotoNum = (parseInt(this.nowpage, 10) + 1 > this.maxPageNum)? this.maxPageNum : (parseInt(this.nowpage, 10) + 1);
      this.options.pageclickfn(gotoNum);
    },
    // [F] 頁碼的計算方法
    calc_fn () {
      let maxPageNum = parseInt(this.maxPageNum, 10),  // 別名
          pagenum = parseInt(this.pagenum, 10),        // 別名
          nowpage = parseInt(this.nowpage, 10),        // 別名
          totalcount = parseInt(this.totalcount, 10),  // 別名
          pcount = parseInt(this.pcount, 10),          // 別名
          pageNumAry = this.pageNumAry,                // 別名
          pagenum_half = Math.ceil(this.pagenum / 2),  // 紀錄一次顯示頁碼數量的一半方便下面計算用
          LR = false,    // 紀錄判斷左右兩邊是否夠放5筆用
          RNum = 0,      // 紀錄右邊放的筆數用
          LNum = 0,      // 紀錄左邊放的筆數用
          isLR = null,   // 區分頁碼是左邊不夠放還是右邊不夠放用
          check = null,  // 記錄不夠放的頁碼數量用
          i = null;      // 迴圈用變數

      // 如果最大頁碼數 小於 套件一次顯示的頁碼數量
      if (maxPageNum < pagenum) {
          pagenum_half = maxPageNum;
      }

      // 先判斷兩邊是否夠放頁碼
      if ((nowpage - pagenum_half >= 1) && (nowpage + pagenum_half <= maxPageNum)) {
          //console.log(nowpage + 5 <= maxPageNum);
          //console.log(nowpage - 5 >= 1);
          LR = true;

          // 夠放: 直接放頁碼數
          for (i = 0; i < pagenum; i++) {
              if (i < (pagenum_half - 1)) {
                  pageNumAry[i] = nowpage - ((pagenum_half - 1) - i); //左邊
              } else {
                  pageNumAry[i] = nowpage + (i - (pagenum_half - 1)); //右邊
              }
          }
      }

      // 左右兩邊不夠放5筆
      if (!LR) {
        // 再區分是左邊不夠放還是右邊不夠放(空字串為預設兩邊都不夠放的意思)....什麼鬼邏輯阿不管我現在只想得到這樣做
        isLR = '';
        if ((nowpage + pagenum_half <= maxPageNum)) isLR = 'left';
        if ((nowpage - pagenum_half >= 1)) isLR = 'right';
        //console.log('不夠的是: ' + isLR);

        // 右邊不夠放
        if (isLR === 'right') {
          // 現在頁碼右邊夠放幾筆
          check = 0;
          for (i = nowpage; i < maxPageNum; i++) {
              check = check + 1; //限制用
              if (check <= (pagenum - LNum))
              RNum = RNum + 1;
          }

          // 現在頁碼左邊夠放幾筆
          check = 0;
          for (i = nowpage; i > (nowpage - ((pagenum - 1) - RNum)); i--) {
              check = check + 1; //限制用
              if (check <= (pagenum - RNum))
              LNum = LNum + 1;
          }
        }

        // 左邊不夠放
        if (isLR === 'left') {
          // 現在頁碼左邊夠放幾筆
          check = 0;
          for (i = nowpage; i > 1; i--) {
            check = check + 1; // 限制用
            if (check <= (pagenum - RNum))
              LNum = LNum + 1;
          }

          // 現在頁碼右邊夠放幾筆
          check = 0;
          for (i = nowpage; i < pagenum; i++) {
            check = check + 1; // 限制用
            if (check <= (pagenum - LNum))
              RNum = RNum + 1;
          }
        }

        // 當左右兩邊都不夠放, 代表資料筆數換算後的頁碼數量小於一次顯示頁碼數量
        if (isLR === '') {
          // 直接給與當下資料換算後的頁碼數量
          for (i = 1; i <= maxPageNum; i++) {
            pageNumAry.push(i);
          }
        } else {
          // 左邊頁碼數
          for (i = 0; i <= LNum; i++) {
            pageNumAry[i] = nowpage - (LNum - i);  // 左邊
          }
          //右邊頁碼數
          for (i = (LNum + 1); i <= (LNum + RNum); i++) {
            pageNumAry[i] = nowpage + (i - LNum);  // 右邊
          }
        }
        //console.log('LR: ' + LR);
        //console.log('RNum: ' + RNum);
        //console.log('LNum:' + LNum);
        //console.log(pageNumAry);
      }
    },
    // [F] 組件初始化的方法
    init () {
      let props = this.options;

      // 防呆
      if(!props.totalcount || !props.pcount || !props.nowpage || !props.pageclickfn) {
        console.log('error: 頁碼套件有必選參數沒有設定');
        return false;
      }

      // 按鈕預設出現
      props.firsthide = (props.firsthide)? true : false;
      props.lasthide = (props.lasthide)? true : false;
      props.prevhide = (props.prevhide)? true : false;
      props.nexthide = (props.nexthide)? true : false;

      // 預設 10 顆頁碼
      props.pagenum = (props.pagenum)? props.pagenum : '10';

      // 將 poprs 的資料轉換成套件內部的 data
      this.totalcount = props.totalcount;
      this.pcount = props.pcount;
      this.pagenum = props.pagenum;
      this.nowpage = props.nowpage;
      this.maxPageNum = Math.ceil(this.totalcount / this.pcount);
      this.pageNumAry = [];

      // 頁碼的計算方法
      this.calc_fn();
    }
  },
  mounted () {
    this.init();
  },
  watch: {
    //監測外部的參數是否有異動
    'options': {
      deep: true, //深度監測的意思
      handler () {
        this.init();
      }
    }
  }
}
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>


