import Vue from 'vue';

// 初始化一個指令定義檔
export const OnlyNum = {
  bind(el, binding, vnode, oldvode) {
    if (!binding.expression) {
      console.log('Directive error: "OnlyNum" must binding a model.');
      return false;
    }
    el.value = binding.value;

    // 定義一個方法在tag上 (方便 unbind 時移除事件註冊)
    el.change = () => {
      if (el.value === '' || isNaN(el.value)) {
        el.value = 0;
        vnode.context[binding.expression] = el.value;
      }
    };
    el.change(); // 初始化先檢查一次
    el.addEventListener('change', el.change, false); // 事件註冊
  },
  unbind (el) {
    el.removeEventListener('change', el.change); // 移除事件註冊
  } 
}
 
// 將此 AnnoyingBackground 定義到 Vue 去
Vue.directive('only-num', OnlyNum)


